import React from 'react';
import { ThemeProvider, createMuiTheme } from '@material-ui/core';
import { deepOrange, blueGrey } from '@material-ui/core/colors';
import { BrowserRouter as Router, Switch, Redirect } from 'react-router-dom';
import PageWrapper from './pages/PageWrapper';
import HomePage from './pages/HomePage';
import ServicesPage from './pages/ServicesPage';
import FadingRoute from './pages/FadingRoute';
import { PageRoutes } from './models/routes';

const theme = createMuiTheme({
  palette: {
    primary: deepOrange,
    secondary: blueGrey,
  },
});

function App() {
  return (
    <Router>
      <ThemeProvider theme={theme}>
        <PageWrapper>
          <Switch>
            <FadingRoute path={PageRoutes.Services} component={ServicesPage} />
            <Redirect from={PageRoutes.Home} to={PageRoutes.Root} />
            <FadingRoute path={PageRoutes.Root} component={HomePage} />
          </Switch>
        </PageWrapper>
      </ThemeProvider>
    </Router>
  );
}

export default App;
