import React from 'react';
import { Route, RouteProps } from 'react-router-dom';
import { Fade } from '@material-ui/core';

type FadingRouteProps = { component: NonNullable<RouteProps['component']> } & RouteProps;
const FadingRoute = ({ component: Component, ...rest }: FadingRouteProps) => {
  return (
    <Route
      {...rest}
      render={routeProps => (
        // <Fade in timeout={5000}>
        <Component {...routeProps} />
        // </Fade>
      )}
    />
  );
};

export default FadingRoute;