import React from 'react';
import { TabProps, Tab, makeStyles, Theme, Popper, Grow, Paper, ClickAwayListener, MenuList, MenuItem } from '@material-ui/core';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: 'flex',
  },
  paper: {
    marginRight: theme.spacing(2),
  },
  link: {
    color: theme.palette.text.primary,
    textDecoration: 'none',
  },
}));

const HeaderTabLink = ({ href, label, ...rest }: TabProps & { href: string }) => {
  const classes = useStyles();
  return (
    <Tab label={<Link className={classes.link} to={href}>{label}</Link>} {...rest} />
  );
};

type HeaderTabPopperProps = TabProps & { href: string, subItems: { href: string, label: TabProps['label'] }[] }
export const HeaderTabWithDropdown = ({ href, label, subItems, ...rest }: HeaderTabPopperProps) => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef<HTMLDivElement>(null);

  const handleClose = (event: React.MouseEvent<EventTarget>) => {
    if (anchorRef.current && anchorRef.current.contains(event.target as HTMLElement)) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event: React.KeyboardEvent) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current!.focus();
    }

    prevOpen.current = open;
  }, [open]);
  return (
    <div className={classes.root}>
      <div onMouseEnter={() => setOpen(true)}
        onMouseLeave={() => setOpen(false)}>
        <Tab
          label={<Link className={classes.link} to={href}>{label}</Link>}
          ref={anchorRef}
          disableFocusRipple
          {...rest}
        />
        <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition>
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
            >
              <Paper>
                <ClickAwayListener onClickAway={handleClose}>
                  <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                    {subItems.map(({ href, label }) => (
                      <MenuItem key={href} onClick={handleClose}>{label}</MenuItem>
                    ))}
                  </MenuList>
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>
      </div>
    </div>
  );
}

export default HeaderTabLink;